import Vue from 'vue';
import AntD from 'ant-design-vue';
import EulerPc from '@euler/pc-ui';
import App from './App.vue';
import router from './router';
import store from './store';
import './style/global.less';
import '@/filters';

Vue.config.productionTip = false;
Vue.use(AntD);
Vue.use(EulerPc);
new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
