<template>
  <a-config-provider :locale="locale" :autoInsertSpaceInButton="false">
    <div v-if="appConfig" id="app">
      <router-view />
    </div>
  </a-config-provider>
</template>
<script>
import zhCN from 'ant-design-vue/lib/locale-provider/zh_CN';

export default {
  data() {
    return {
      locale: zhCN,
    };
  },
  computed: {
    // 使用getters获取全局appConfig
    appConfig() {
      return this.$store.getters.getAppConfig;
    },
  },
};
</script>
<style lang="less" scoped>
</style>
