const { toString } = Object.prototype;
// 公共组件沉淀方法库导入导出
export * from '@/components/_utils';
export const isArray = (value) => {
  if (typeof Array.isArray === 'function') {
    return Array.isArray(value);
  } else {
    return toString.call(value) === '[object Array]';
  }
};
export const isObject = (value) => {
  return toString.call(value) === '[object Object]';
};

export const isNull = (value) => {
  return value === null;
};

export const isUndefined = (value) => {
  return value === undefined;
};

export const isString = (value) => {
  return typeof value === 'string';
};

export const isEmpty = (value) => {
  if (isArray(value)) {
    return value.length === 0;
  }

  if (isObject(value)) {
    return Object.keys(value).length === 0;
  }

  return value === '' || isUndefined(value) || isNull(value);
};

export const last = (data) => {
  if (isEmpty(data)) return;

  if (isArray(data) || isString(data)) {
    return data[data.length - 1];
  }
};

/**
 * @param {HTMLElement} element
 * @param {string} event
 * @param {function} handler
 */
export const on = (() => {
  if (document.addEventListener) {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.addEventListener(event, handler, false);
      }
    };
  } else {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.attachEvent(`on${event}`, handler);
      }
    };
  }
})();
/**
 * @param {HTMLElement} element
 * @param {string} event
 * @param {function} handler
 */
export const off = (() => {
  if (document.removeEventListener) {
    return function (element, event, handler) {
      if (element && event) {
        element.removeEventListener(event, handler, false);
      }
    };
  } else {
    return function (element, event, handler) {
      if (element && event) {
        element.detachEvent(`on${event}`, handler);
      }
    };
  }
})();

/**
 * @param {HTMLElement} el
 * @param {string} event
 * @param {function} fn
 */
export const once = (el, event, fn) => {
  const listener = function (...args) {
    if (fn) {
      fn.apply(this, args);
    }
    off(el, event, listener);
  };
  on(el, event, listener);
};

/**
 * @param {array} list
 * @param {*} predicate
 * @returns {array}
 */
export const uniqBy = (list, predicate) => {
  if (isEmpty(list)) return [];

  const cb = typeof predicate === 'function' ? predicate : (o) => o[predicate];
  return [
    ...list
      .reduce((map, item) => {
        const key = isEmpty(item) ? item : cb(item);
        map.has(key) || map.set(key, item);
        return map;
      }, new Map())
      .values(),
  ];
};

/**
 * 防抖
 * @param {function} fn
 * @param {number} delay
 * @returns
 */
export const debounce = (fn, delay) => {
  let timer = null;

  return function (...args) {
    const context = this;

    if (timer) {
      clearTimeout(timer);

      timer = setTimeout(() => {
        fn.apply(context, args);
      }, delay);
    } else {
      timer = setTimeout(() => {
        fn.apply(context, args);
      }, delay);
    }
  };
};
/**
 * @description 由于现在看到基本上接口树状数据都是统一这种结构 `{main:object,children:object[]}` 在这里统一处理成`{[key:string]:any,children:object[]}[]`
 * @param {{main:object,children:object[]} | {main:object,children:object[]}[]} data
 * @param {{children: string;title: string;key: string;value: string;}} props
 * @param {number?} level
 * @returns {{[key:string]:any,children:object[]}[]}
 * @example tree组件渲染只需要修改replaceFields
 * js
 *  import { deepTree } from "@/utils/utils";
 *  this.treeData = deepTree(res.orgTreeDate)
 * html
 *  <a-tree
 *    :tree-data="treeData"
 *    :replaceFields="{
 *      key: 'orgUniqueCd',
 *      title: 'orgName',
 *  }"
 *  />
 */
const deepTreeProps = {
  children: 'children',
  title: 'title',
  key: 'key',
  value: 'value',
};
export const deepTree = (data = [], props = {}, level = 1) => {
  const nProps = {
    ...deepTreeProps,
    ...props,
  };
  if (isEmpty(data)) return [];
  const list = (isObject(data) ? [data] : data) || [];
  const len = list.length;
  const result = [];
  for (let i = 0; i < len; i++) {
    const item = list[i];
    const children = item[nProps.children] ?? [];
    const isLeaf = !children.length;

    const nItem = {
      _level: level,
      ...item.main,
      isLeaf,
    };

    nItem.key = nItem[nProps.key];
    nItem.title = nItem[nProps.title];
    nItem.value = nItem[nProps.value];

    if (!isLeaf) {
      nItem.children = deepTree(children, nProps, level + 1);
    }
    result.push(nItem);
  }
  return result;
};

export const fileSaveAs = (file, name) => {
  if (!file) return;
  const url = window.URL.createObjectURL(file);
  const link = document.createElement('a');
  link.style.display = 'none';
  link.href = url;
  link.setAttribute('download', name);
  document.body.appendChild(link);
  link.click();
};
export const weightMatching = (fields, data, unit, type) => {
  // 重量匹配
  return data?.map((item) => {
    const obj = { ...item };
    for (const k in item) {
      if (fields.includes(k)) {
        if (type === '/') {
          obj[k] /= unit;
        } else {
          obj[k] *= unit;
        }
      }
    }
    return obj;
  });
};
// 判断是否是字符串数字
export const isNumber = (str) => !isNaN(Number(str));

/**
 * 下载文件
 * @param url
 * @param filename
 */
export const DownloadA = (url, filename) => {
  if (!url) return;
  const link = document.createElement('a'); //  创建a标签
  link.style.display = 'none'; //  使其隐藏
  link.href = url; // 赋予文件下载地址
  link.setAttribute('download', filename); // 设置下载属性 以及文件名
  document.body.appendChild(link); // a标签插至页面中
  link.click(); //  强制触发a标签事件
  document.body.removeChild(link);
};
