import cloneDeep from 'lodash/cloneDeep';
import isPlainObject from 'lodash/isPlainObject';
/**
 * 平铺树结构转换标注树结构
 * @param {Array} list 平铺类型数据
 * @param {Object | Null} replace 替换属性名称，key原来属性，value新属性
 * @param {Object} option 对应replace中的key字段，pkey和children必填
 * @returns treeData树结构
 */
export function listFormatTree(
  list = [],
  replace = { pkey: 'pkey', key: 'key', title: 'title', children: 'children' },
  option = { pkey: 'pkey', children: 'children' },
) {
  if (!Array.isArray(list)) {
    return console.error('请传入一个list数组');
  }
  list.forEach((child) => {
    const parentId = child[option.pkey];
    if (parentId) {
      list.forEach((parent) => {
        const parentTmp = parent;
        // 替换属性
        if (replace) {
          for (const key in replace) {
            // 新旧属性不一致的替换
            if (String(key) !== String(replace[key])) {
              if (
                Array.isArray(parentTmp[replace[key]]) ||
                parentTmp[replace[key]] instanceof Object
              ) {
                parentTmp[key] = cloneDeep(parentTmp[replace[key]]);
              } else {
                parentTmp[key] = parentTmp[replace[key]];
              }
              Reflect.deleteProperty(parentTmp, replace[key]);
            }
          }
        }
        if (parentTmp && parentTmp.key === parentId) {
          parentTmp[option.children] = parentTmp[option.children] || [];
          parentTmp[option.children].push(child);
        }
      });
    }
  });
  const root = list.every((item) => item[replace.pkey]);
  if (root) {
    return console.error('至少有一个根节点pid为null数组或传入一个空数组', list);
  }
  return list.filter((n) => !n[option.pkey]);
}

/**
 * 树转换为扁平树结构
 * @param {Array} treeData 树结构
 * @param {Object | Null} replace 替换属性名称，value要替换的属性名，null 不替换
 * @param {String} children 对应replace中children字段
 * @returns
 */
export function treeFormatList(
  treeData = [],
  replace = { pkey: 'pkey', key: 'key', title: 'title' },
  children = 'children',
) {
  if (!Array.isArray(treeData)) {
    return console.error('请传入一个tree数组');
  }
  const list = [];
  const clone = cloneDeep(treeData);
  const stack = [...clone];
  while (stack.length) {
    const node = stack.shift();
    const child = node[children];
    if (child) {
      stack.push(...child);
    }
    // 如果存在replace那就依次更换属性名称
    if (replace) {
      for (const key in replace) {
        if (String(key) !== String(replace[key])) {
          if (Array.isArray(node[replace[key]]) || node[replace[key]] instanceof Object) {
            node[key] = cloneDeep(node[replace[key]]);
          } else {
            node[key] = node[replace[key]];
          }
          Reflect.deleteProperty(node, replace[key]);
        }
      }
    }
    // 删除空children属性
    Reflect.deleteProperty(node, children);
    list.push(node);
  }
  // 如果本身是list就返回本身
  return list.length === 0 ? treeData : list;
}

/**
 * 过滤树，并保留原有的结构
 * @param {Array} treeData
 * @param {Function} filterFn 回调函数值判断来过滤，默认true不过滤
 * @param {String} children 子节点字段属性名称
 * @returns
 */
export function filterTree(treeData = [], filterFn = () => true, children = 'children') {
  if (!Array.isArray(treeData)) {
    return console.error('请传入一个tree数组');
  }
  function filter(treeDatas) {
    if (!treeDatas?.length) {
      return treeDatas;
    }
    return treeDatas.filter((data) => {
      const item = data;
      if (!filterFn(item)) {
        return false;
      }
      item[children] = filter(item[children]);
      return true;
    });
  }

  return filter(treeData) || [];
}

/**
 * 树转为list并且过滤返回过滤的列表
 * @param {Array} treeData 树结构数据
 * @param {Function} filterFn 回调函数值判断来过滤，默认true不过滤
 * @param {Object | Null} replace 替换属性名称，value要替换的属性名，null 不替换
 * @returns 过滤后的数据
 */
export function filterList(
  treeData,
  filterFn = () => true,
  replace = { pkey: 'pkey', key: 'key', title: 'title' },
) {
  if (!Array.isArray(treeData)) {
    return console.error('请传入一个tree数组');
  }
  const clone = cloneDeep(treeData);
  const list = treeFormatList(clone, replace);
  return list.filter((item) => {
    if (!filterFn(item)) {
      return false;
    }
    return true;
  });
}

/**
 * 数组去重
 * @param {Array} arr 存在重复数组对象
 * @param {String} prop 依据该属性判断唯一
 * @returns
 */
export function uniqueArrayBy(arr = [], prop = 'key') {
  if (!Array.isArray(arr)) {
    return console.error('请传入一个数组');
  }
  if (arr && arr.length !== 0 && isPlainObject(arr[0])) {
    const map = new Map();
    arr.forEach((item) => {
      if (item[prop]) {
        map.set(item[prop], item);
      } else {
        throw new Error('prop参数不匹配，在节点找不到对应的值');
      }
    });
    if (map.size !== 0) {
      return Array.from(map.values());
    }
  } else {
    return Array.from(new Set(arr));
  }
}

/**
 * 获取选中节点展开的keys
 * @param {Array} treeData 树data
 * @param {String} selected 选中的节点
 * @param {Object | Null} replace 替换属性名称，value要替换的属性名，null 不替换
 * @param {Object} option 默认配置
 * @returns {Array} 路径数组
 */
export function getExpandedKeys(
  treeData,
  selected,
  replace = { pkey: 'pkey', key: 'key', title: 'title' },
  option = { pkey: 'pkey', key: 'key' },
) {
  if (!Array.isArray(treeData)) {
    return console.error('请传入一个tree数组');
  }
  const paths = new Set();
  const data = treeFormatList(treeData, replace);
  function fn(list, select) {
    list.forEach((node) => {
      if (String(node[option.key]) === String(select)) {
        paths.add(node[option.key]);
        // 移除叶子节点
        if (node.isLeaf) {
          paths.delete(node[option.key]);
        }
        fn(list, node[option.pkey]);
      }
    });
  }
  fn(data, selected);
  return Array.from(paths)
    .reverse()
    .filter((item) => item !== selected);
}

/**
 * 获取选中节点选择的keys
 * @param {Array} treeData 树data
 * @param {String} selected 选中的节点
 * @param {String} by 依据by字段取值，默认配置'key'
 * @param {Object | Null} replace 替换属性名称，value要替换的属性名，null 不替换
 * @param {Object} option 默认配置
 * @returns {Array} 路径数组
 */
export function getSelectedBy(
  treeData,
  selected,
  by = 'key',
  replace = { pkey: 'pkey', key: 'key', title: 'title' },
  option = { pkey: 'pkey', key: 'key' },
) {
  if (!Array.isArray(treeData)) {
    return console.error('请传入一个tree数组');
  }
  const keys = new Set();
  const data = treeFormatList(treeData, replace);
  function fn(list, select) {
    list.forEach((node) => {
      if (String(node[option.key]) === String(select)) {
        keys.add(node[by]);
        fn(list, node[option.pkey]);
      }
    });
  }
  fn(data, selected);
  return Array.from(keys).reverse();
}

/**
 * 根据选中的属性获取节点node
 * @param {Array} treeData 树data
 * @param {String} selected 选中的节点值
 * @param {String} by 属性名称
 * @param {Object | Null} replace 替换属性名称，key原来属性，value新属性
 * @returns {Object} 选中的节点对象
 */
export function getNodeBy(
  treeData,
  selected,
  by = 'key',
  replace = { pkey: 'pkey', key: 'key', title: 'title' },
) {
  if (!Array.isArray(treeData)) {
    return console.error('请传入一个tree数组');
  }
  const data = treeFormatList(treeData, replace);
  const node = data.filter((n) => {
    return String(n[by]) === String(selected);
  });
  return Array.isArray(node) && node.length === 1 ? node[0] : null;
}

/**
 * 根据选中的属性获取对应的name名称
 * @param {Array} dicList 字典列表
 * @param {String} value 节点值
 * @param {String} option 字典中key和label字典对应字段名称
 * @returns {String} 值对应的属性
 */
export function getNameBy(dicList, value, option = { key: 'key', label: 'title' }) {
  if (!Array.isArray(dicList)) {
    return console.error('请传入一个字典数组');
  }
  const node = dicList.filter((n) => {
    return String(n[option.key]) === String(value);
  });
  // console.log(node)
  if (node && node.length !== 0) {
    return node[0][option.label];
  }
  return '';
}
