import Vue from 'vue';
import moment from 'moment';
import { getNameBy } from '@/utils/utils';

export const dateFormat = (val, format = 'YYYY-MM-DD HH:mm:ss') => {
  let time = val;
  if (!time) return;
  let date = moment(time);
  if (!date.isValid()) {
    if (time.includes(':')) {
      time = time.replaceAll(':', '');
    }
    const year = time.slice(0, 4);
    const month = time.slice(4, 6);
    const day = time.slice(6, 8);
    const hour = time.slice(8, 10) || '00';
    const minute = time.slice(10, 12) || '00';
    const second = time.slice(12, 14) || '00';

    date = moment([[year, month, day].join('-'), [hour, minute, second].join(':')].join(' '));
  }

  return date.format(format);
};

/**
 * 格式化时间过滤器
 * @example
 * {{ date | dateFormat(YYYY-MM-DD')}}
 */
Vue.filter('dateFormat', (value, format) => {
  // 返回处理后的值
  if (!value) return '';
  return dateFormat(value, format);
});

/**
 * 转换编码为名称过滤器
 * @example
 * {{ status | getNameBy([{key: 'zh', title: '中文名'}],{key: 'key', label: 'title'})}}
 */
Vue.filter('getNameBy', (value, dic, option = { key: 'key', label: 'title' }) => {
  // 返回处理后的值
  if (!value) return '';
  // console.log('dic, value, option', dic, value, option)
  return getNameBy(dic, value, option);
});

/**
 * @Description 数量倍数转换过滤器
 * @param multiple 倍数
 * @return 数量
 */
Vue.filter('amountFormat', (value, multiple) => {
  const amount = Number(value);
  const multipleNum = Number(multiple);
  if (isNaN(amount) || isNaN(multipleNum)) {
    return value;
  } else {
    return (amount / multipleNum).toFixed(2);
  }
});
